.filter_periode {
  @extend .drop_tabs;

  &.on_mobile {
    h3 {
      margin: 2px 10px;
    }
    .content {
      padding: 10px 5px;
      .item {
        padding: 5px 10px;
      }
    }
  }

  border: 1px solid var(--border);
  padding: 5px;
  .content {
    .item.default {
      opacity: 0.7;
      pointer-events: none;
    }
    .item {
      background-color: var(--primary);
      font-size: 14px;
      color: #ffffff;
      border-color: transparent;
      padding: 0 10px;
      cursor: auto;
      i {
        margin-left: 20px;
        font-size: 14px;
        cursor: pointer;
        color: #ffffff;
      }
      i:hover {
        font-size: 16px;
      }
    }
  }
}
