.user_card {
  display: grid;
  grid-template-columns: 150px auto;
  gap: 10px;
  padding-left: 20px;

  a {
    text-decoration: none;
    color: var(--writing);
    font-family: "time new roman";
    font-weight: 400;
    text-transform: capitalize;
  }
  .image {
    position: relative;
    height: 150px;
    width: 150px;
    border-radius: 5px;
    background-size: cover;
    background-color: var(--background);
    box-shadow: 2px 0 5px var(--shadow);
    transition: all 0.15s ease-in-out;

    &:hover {
      box-shadow: -2px 0 5px var(--shadow);
    }
    i {
      position: absolute;
      bottom: 5px;
      right: 5px;
      background-color: var(--accent);
      cursor: pointer;
      padding: 5px;
    }
  }
  .description p {
    margin-bottom: 8px;
  }
  .text-content {
    padding: 15px 20px 10px 15px;
    display: flex;
    flex-direction: column;

    h3 {
      font-size: 1.6em;
    }

    button {
      @extend %button;
    }
    .update {
      height: 30px;
      float: right;
    }
  }
}

@media screen and (max-width: 540px) {
  .user_card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 0;
    .text-content {
      width: 100%;
      text-align: center;

      .update {
        position: relative;
        height: 40px;
        margin: auto;
        margin-top: 30px;
        float: none;
      }
    }
  }
  .image {
    border-left: 4px solid transparent;
  }
}
