#about_page {
  section {
    display: flex;
    width: 100%;
  }
  main {
    padding: 0;
  }
  .aside_about {
    width: auto;
    padding: 25px;
    min-width: 400px;
    .app_title {
      margin-bottom: 20px;
      font-size: 50px;
      font-family: "Poppins", Arial, sans-serif;
      font-weight: 600;
      line-height: 1.2;
      color: var(--writing);
      strong {
        color: var(--primary);
      }
    }
    .text_about {
      text-align: justify;
    }
  }
  .image_box {
    @extend %center;
    img {
      width: 100%;
    }
  }
}

@media screen and (max-width: 850px) {
  #about_page {
    section {
      flex-direction: column;
    }
  }
}
