.drop_tabs {
  display: flex;
  flex-direction: column;
  height: fit-content;
  border-top: 1px solid var(--border);
  .title_bar {
    height: 50px;
    display: grid;
    grid-template-columns: 25px auto 50px;
    line-height: 50px;
    background-color: var(--background);
    font-weight: bold;
    // color: #3f3e3e;
    cursor: pointer;
    i {
      @extend %center;
      transition: transform 0.5s;
      color: var(--writing);
    }
    .fa-angle-down {
      background-color: var(--accent);
      border-radius: 50%;
      margin: 10px;
      font-size: 20px;
    }
  }
  .content {
    display: flex;
    height: fit-content;
    flex-wrap: wrap;
    gap: 10px;
    padding: 10px 0;
    padding-left: 20px;
    max-width: 230px;
    overflow-y: scroll;
    overflow-x: hidden;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }

    .item {
      background-color: var(--background);
      border: 1px solid var(--border);
      @extend %center;
      padding: 2px 10px;
      border-radius: 5px;
      animation: show_toast_mobile 0.5s linear;
      max-width: 100%;
      word-break: break-all;
      cursor: pointer;

      &:hover {
        border-color: var(--primary);
      }
    }
    .item.selected {
      background-color: var(--primary);
      color: white;
      border: none;
    }
  }
  .content.hide {
    // opacity: 0;
    pointer-events: none;
    transform-origin: top;
    padding: 0 !important;
    height: 0 !important;
  }
  .hide .item {
    animation: fadeOut 0.5s linear forwards;
  }
  .rotate {
    transform: rotate(180deg) !important;
  }
}
