.start_component {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #cacacacb;
  top: 0;
  position: absolute;
  z-index: 2;
  border-radius: 0;

  .content_slide {
    background-color: var(--background);
    height: 60vh;
    width: 550px;
    padding: 10px;
    border: 1px solid var(--borber);
    display: grid;
    grid-template-rows: 85% auto;

    .welcome {
      @extend %center;

      flex-direction: column;
      img {
        height: calc(100% - 10px);
        max-height: 280px;
      }
      .text {
        text-align: center;
      }
    }
    .welcome.end {
      padding: 10px;
    }
    .cycle_stape {
      height: 100%;
      padding: 20px;
      display: grid;
      grid-template-rows: 20px calc(100% - 80px) 60px;

      img {
        height: 100%;
        margin: auto;
      }

      .drop_tabs {
        border: 1px solid var(--border);

        .title_bar {
          padding: 0 10px;
          display: none;
        }
        .content {
          max-width: none;
          justify-content: space-around;
        }
        .content .item {
          padding: 10px;
        }
      }
      .text {
        text-align: center;
        text-transform: capitalize;
        font-weight: 500;
      }
    }

    .navigation {
      @extend %center;
      justify-content: space-between;
      position: relative;
      padding: 0 10px;

      .stapes {
        display: flex;
      }
      .stape {
        margin: 0 4px;
        height: 20px;
        width: 20px;
        background-color: gray;
        cursor: pointer;
      }
      .stape.active {
        background-color: var(--primary);
      }
      .next.disable {
        background-color: var(--secondary);
        cursor: not-allowed;
      }
      .next {
        @extend %button;
        height: 30px;
        // position: absolute;
        // right: 10px;
      }
      .prev {
        @extend %button-outline;
        // position: absolute;
        // left: 10px;
        height: 30px;
        color: var(--writing);
        i {
          color: var(--writing);
        }
      }
      i {
        color: #ffffff;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .start_component {
    background-color: var(--background);

    .navigation {
      padding: 0;
    }
    .content_slide {
      width: 100vw;
      height: 60vh;
      padding: 0 20px;
      border-radius: 0;
    }
  }
}
