.top_bar {
  padding: 15px 0;
  // margin: 0 15px; FOR MOBILE
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  text-transform: capitalize;
  border-bottom: 1px solid var(--border);
  border-radius: 0;

  i {
    font-weight: bold;
    cursor: pointer;
    background-color: var(--accent);
    height: 35px;
    width: 35px;
    @extend %center;
    border-radius: 4px;
  }
  .disable {
    // pointer-events: none;
    color: #919191;
    cursor: not-allowed;
  }
  .arian {
    opacity: 0.7;
  }
}

