#LogIn_page {
  .nav_login {
    @extend %link;
    ul {
      display: flex;
      justify-content: space-around;
    }
    ul span {
      cursor: pointer;
    }
  }
  #message_box {
    padding: 10px;
    animation: swing 500ms ease;
    color: white;
  }
  #message_box.success {
    background-color: var(--success);
  }
  #message_box.error {
    background-color: var(--danger);
  }
  #message_box.warning {
    background-color: var(--warning);
  }
  sup {
    color: var(--danger);
  }

  .login_form {
    padding: 40px;

    .user_name {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 10px;
    }
    form {
      display: flex;
      flex-direction: column;
      gap: 2px;

      span {
        font-size: 14px;
        color: var(--red);
        margin-top: -8px;
        display: none;
        animation: swing 500ms ease;
      }

      .input_group {
        @extend %search_group;
        input {
          height: 40px;
          transition: all 300ms linear;
        }
        i {
          color: var(--writing);
        }
      }
    }
    input {
      @include input($height: 40px);
      padding-right: 10px;
      margin: 5px 0;
    }
    button {
      @extend %button;
    }
  }
  .select_search_custom {
    height: 40px;
    width: 100%;
    position: relative;
    cursor: pointer;
    @extend %center;
    justify-content: flex-start;
    display: flex;

    input {
      @include input();
      height: 100%;
    }
    .options.hide {
      opacity: 0;
      transform: scaleY(0);
    }

    i {
      position: absolute;
      pointer-events: none;
      font-size: 22px;
      font-weight: bold;
      height: 100%;
      width: 40px;
      top: 0;
      right: 0;
      @extend %center;
      transition: all 0.5s ease;
    }
    i.rotate {
      transform: rotate(-90deg);
    }
    .options {
      opacity: 1;
      transform-origin: top;
      transition: all 0.3s ease-in-out;
      position: absolute;
      height: 200px;
      border-radius: 4px;
      width: 100%;
      top: 45px;
      right: 0;
      background-color: var(--background);
      border: 1px solid gray;
      padding: 10px;
      overflow-x: hidden;
      overflow-y: scroll;
      height: 180px;

      .option {
        height: 40px;
        border-bottom: 1px solid gray;
        @extend %center;
        justify-content: flex-start;
        padding-left: 5px;
        transition: all 0.1s linear;

        &:hover {
          background-color: var(--primary);
          color: white;
        }
      }
    }
  }
}
