.pagination {
  display: flex;
  padding: 10px;
  margin: 30px 0;
  gap: 20px;
  @extend %center;
  border: 1px solid var(--border);

  button {
    @extend %button-outline;
  }
  .disable {
    cursor: not-allowed;
    border: 1px solid var(--border);
  }
  .pages {
    span {
      border: 1px solid var(--border);
      padding: 5px 10px;
      margin: 0 5px;
      cursor: pointer;
      &:hover {
        border-color: var(--primary);
      }
    }
  }
}
