.box_confirm {
  position: fixed;
  top: calc(50% - 215px);
  margin: auto;
  right: calc(50% - 215px);
  z-index: 10;
  animation: show_toast_mobile 1s ease forwards;

  &.hide {
    animation: hide_toast_mobile 1s ease forwards;
  }
  .touch_screen {
    content: "";
    position: absolute;
    height: 200vh;
    width: 200vw;
    top: -50vh;
    right: -50vw;
    background-color: #00000031;
    z-index: -1;
  }

  .toast {
    background: var(--background);
    padding: 20px 15px 20px 20px;
    border-radius: 5px;
    border: 1px solid gray;
    width: 430px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: space-between;
  }

  button {
    @extend %button-outline;
    margin-top: 10px;
  }
  .btn_comfir {
    margin-right: 10px;
  }
  .btn_cancel {
    border-color: var(--secondary);
    margin-left: 10px;
  }

  .icon {
    font-size: 25px;
    height: 50px;
    width: 50px;
    text-align: center;
    line-height: 50px;
    border-radius: 50%;

    i {
      color: #fff;
    }
  }
  .details {
    grid-column: span 2;
    padding: 10px;
  }
  .details span {
    font-size: 20px;
    font-weight: 500;
  }
  .details p {
    color: var(--writing);
  }
  .toast .close-icon {
    color: var(--writing);
    font-size: 23px;
    cursor: pointer;
    height: 30px;
    width: 30px;
    text-align: center;
    line-height: 30px;
    border-radius: 50%;
    background: var(--accent);
    transition: all 0.3s ease;
    position: absolute;
    top: 20px;
    right: 20px;
  }
  .close-icon:hover {
    background: var(--input-accent);
  }
}

@media screen and (max-width: 470px) {
  .box_confirm {
    right: 15px;
    top: 50px;
    width: calc(100vw - 35px);

    .toast {
      width: 100%;
    }
  }
}
