#side_menu {
  z-index: 2;
  width: 250px;
  height: 100vh;
  background: var(--background);
  position: sticky;
  top: 0;
  padding-top: 5em;
  padding-bottom: 40px;
  padding-left: 2em;
  padding-right: 2em;
  transition: all 0.3s ease;
  border-right: 1px solid var(--border);
  border-radius: 0;

  .log_out_btn {
    position: absolute;
    bottom: 30px;
    cursor: pointer;
    font-family: "Poppins", Arial, sans-serif;

    &:hover {
      color: var(--primary);
    }
    i {
      background-color: var(--accent);
      border: 1px solid var(--border);
      height: 35px;
      width: 35px;
      line-height: 35px;
      padding: auto;
      border-radius: 5px;
      text-align: center;
    }
  }

  .nav_list {
    @extend %link;
    width: inherit;

    ul li i {
      background-color: var(--accent);
      height: 30px;
      width: 30px;
      line-height: 30px;
      padding: auto;
      border-radius: 5px;
      text-align: center;
    }
  }

  .mobile_top_bar {
    @extend %center;
    position: absolute;
    right: 0px;
    top: 0;
    height: 50px;
    width: 50px;
    background-color: transparent;
    color: var(--writing);
    cursor: pointer;
    transition: all 0.2s ease;
  }
  &.close {
    margin-right: 50px;
    .mobile_top_bar {
      right: -50px;
    }
    margin-left: -250px;
  }
  .overlay {
    display: none;
  }
}

@media screen and (max-width: 950px) {
  #side_menu {
    position: fixed;
    &.close {
      margin-right: 0;
    }
    &.close .overlay {
      display: none;
    }
    .overlay {
      display: block;
      height: inherit;
      background-color: var(--background);
      opacity: 0.6;
      width: 100vw;
      position: absolute;
      top: 0;
      left: 100%;
    }
  }
}
