#aside_bar {
  min-width: 300px;
  max-height: 100vh;
  position: sticky;
  top: -1px;
  height: fit-content;
  min-height: 100vh;
  transition: all 0.3s ease;
  display: flex;
  gap: 20px;
  flex-direction: column;
  padding-top: 50px;
  border-left: 1px solid var(--border);
  border-radius: 0;

  & > .content {
    overflow-x: hidden;
    padding: 30px 20px 32px 20px;
    overflow-y: scroll;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    &::-webkit-scrollbar-thumb {
      background-color: transparent;
      // border-radius: 10px;
      max-width: 5px;
    }
    &::-webkit-scrollbar-track {
      background-color: transparent;
      padding: 2px;
      box-shadow: inset 0 0 4px transparent;
    }
  }
  .mobile_top_aside {
    display: none;
    @extend %center;
  }
  form {
    .search_group {
      @extend %search_group;
    }
  }
}

@media screen and (max-width: 950px) {
  #aside_bar {
    position: fixed;
    top: -1px;
    right: 0;

    .mobile_top_aside {
      border-left: 1px solid var(--border);
      display: flex;
      position: absolute;
      left: -1px;
      top: -1px;
      height: 50px;
      width: 50px;
      color: var(--writing);
      cursor: pointer;
      font-size: 22px;

      &::after {
        content: "";
        pointer-events: none;
        z-index: -1;
        position: absolute;
        top: -1px;
        right: 48px;
        height: inherit;
        width: 100vw;
        background-color: var(--background);
      }
    }
    &.close {
      .mobile_top_aside {
        border-left: 1px solid transparent;
        left: -50px;
        background-color: var(--background);
      }
      margin-right: -300px;
    }
  }
}

@media screen and (max-width: 620px) {
  #aside_bar {
    position: fixed;
    top: -1px;
    right: 0;
    width: 100vw;

    .mobile_top_aside {
      position: fixed;
      border-left: 1px solid transparent;
      right: 0;
      left: auto;
    }
    &.close {
      .mobile_top_aside {
        left: calc(100vw - 55px);
        background-color: var(--background);
      }
      margin-right: -100vw;
    }
  }
}
