#book_details {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 15px;

  .doc_preview {
    @extend %center;
    background-size: cover;
    height: 280px;
    width: 200px;
    border: 1px solid #bbbbbb;
    position: relative;

    button {
      @extend %button-outline;
      position: absolute;
      bottom: -50px;
      width: 100%;
      left: 0;
    }
  }

  .description {
    padding: 0 10px 50px 10px;

    .text_box {
      text-align: justify;
    }
    .info {
      margin: 20px 0;
      font-weight: 500;
      letter-spacing: 1px;
      font-size: 18px;
    }
  }

  .user_detail {
    display: grid;
    grid-template-columns: 70px auto;
    gap: 5px;
    margin-bottom: 20px;

    a {
      text-decoration: none;
      color: var(--writing);
      font-weight: 400;
      text-transform: capitalize;
    }
    .image {
      height: 70px;
      width: 70px;
      border-radius: 50%;
      background-size: cover;
      background-color: var(--background);
      transition: all 0.15s ease-in-out;
      cursor: pointer;
      border-radius: 5px;
      border: 1px solid #bbbbbb;

      a {
        height: 100%;
        width: 100%;
      }
    }
    .text-content {
      padding-left: 8px;
      display: flex;
      flex-direction: column;

      p {
        opacity: 0.6;
      }
    }
    .option {
      @extend %button-outline;
      height: 25px;
      margin-top: 5px;
      border-color: var(--success);
      width: fit-content;
    }
  }
  .theme {
    font-family: "Times New Roman", Times, serif;
    text-transform: capitalize;
    font-weight: 500;
  }
}

@media screen and (max-width: 1100px) {
  #book_details {
    display: flex;
    flex-direction: column;
  }
}
@media screen and (max-width: 950px) {
  #book_details {
    display: grid;
  }
}
@media screen and (max-width: 790px) {
  #book_details {
    display: flex;
    align-items: center;
    width: 100%;

    .doc_preview {
      height: 350px;
      width: calc(100vw - 60px);
      max-width: 300px;
      margin-bottom: 80px;
    }
  }
}
