.level_tags {
  display: grid;
  grid-gap: 10px;
  height: -moz-fit-content;
  height: fit-content;
  grid-template-columns: auto auto auto;
  margin: 20px 0;
  background: var(--background);
  padding: 10px;
  border-radius: 5px;
  border: 1px solid var(--border);

  .level_radio:checked + label {
    background-color: var(--primary);
    border-color: var(--primary);
  }

  .item {
    background-color: var(--background);
    border: 1px solid var(--border);
    padding: 0 10px;
    border-radius: 5px;
    animation: show_toast_mobile 0.5s linear;
    cursor: pointer;
    min-width: 20%;
    text-align: center;
    display: grid;
    grid-template-columns: 20px auto;
  }
  p {
    grid-column: span 3;
  }
  span {
    display: block;
  }
}

@media screen and (max-width: 390px) {
  .level_tags {
    display: flex;
    flex-direction: column;
  }
}
