.select_custom {
  i {
    height: 100%;
    width: 40px;
    line-height: 40px;
    text-align: center;
    color: var(--primary);
  }
  select {
    @include input();
    width: auto;
    border-radius: 10px;
    option {
      opacity: 0.5;
      font-size: 14px;
      text-align: justify;
      border: none;
    }
  }
  .menu {
    display: flex;
    gap: 10px;
    align-items: center;
  }
  .select {
    position: relative;
    width: fit-content;
    text-transform: lowercase;
    #select_value {
      @include input($width: 230px);
      overflow: hidden;
      border: none;
      padding: 0 12px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 16px;
      cursor: pointer;
      background-color: white;

      i {
        transition: transform 0.5s;
        height: auto;
        width: auto;
        line-height: 40px;
        text-align: center;
      }
    }
    #list_option {
      position: absolute;
      width: 100%;
      background: white;
      margin-top: 5px;
      border: 1px solid gray;
      animation: showOp 0.1s ease-in-out;
      z-index: 10;
      padding: 5px;
      max-height: 200px;
      overflow-x: hidden;
      overflow-y: scroll;
      .options {
        width: 100%;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        display: flex;
        font-size: 15px;
        gap: 5px;
        max-height: 40px;
        line-height: 40px;
        border-bottom: 1px solid gray;
        &:last-child {
          border-bottom: 0;
        }
        i {
          pointer-events: none;
          font-size: 8px;
          color: var(--writing);
        }
        span {
          pointer-events: none;
          width: 100%;
        }
        &:hover {
          background: var(--green-70);
        }
        &:hover i {
          color: var(--primary);
        }
      }
    }
    .hide {
      opacity: 0;
      pointer-events: none;
    }
    .rotate {
      transform: rotate(180deg) !important;
    }
  }
}

@media screen and (max-width: 600px) {
  .select_custom {
    p {
      text-align: center;
    }
    .select {
      width: 100%;
      #list_option {
        .options {
          font-size: 18px;
        }
      }
    }
  }
}
