.profile_page {
  // color: #5c5c5c;

  .user_book {
    padding: 20px;
    margin-top: 30px;
    border-top: 1px solid var(--border);
    display: flex;
    flex-direction: column;
    gap: 20px;

    .header {
      display: flex;
      justify-content: space-between;
    }
    button {
      @extend %button;
      height: 30px;
    }
  }
}

@media screen and (max-width: 670px) {
  .profile_page {
    .user_book {
      .header {
        display: flex;
        flex-direction: column;
        h3 {
          text-align: center;
        }
      }
      button {
        height: 40px;
        margin: 20px 0;
        width: 100%;
      }
    }
  }
}
@media screen and (max-width: 570px) {
  .profile_page {
    .add_book_form {
      display: flex;
      flex-direction: column;

      .files_upload {
        width: 85vw;
        margin: auto;

        .lable_file {
          height: 50px;
        }
      }
    }
  }
}
