* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-radius: 4px;
  position: static;
  // overflow-x: hidden;
  color: var(--writing);
  font-family: $font;
}
html {
  overflow-x: hidden;
}

body {
  background-color: var(--background);
  margin: 0;
  font-family: $font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
