.add_book_form {
  display: grid;
  grid-template-columns: 200px auto;
  grid-gap: 30px;
  padding: 20px;
  padding-top: 40px;
  margin-top: 27px;
  border-top: 1px solid var(--border);
  position: relative;

  input,
  textarea {
    @include input(40px);
  }
  button {
    @extend %button;
  }

  sup {
    font-size: large;
    color: var(--danger);
    line-height: 4px;
  }

  .help_message {
    font-size: 14px;
    display: block;
    color: var(--danger);
    opacity: 0.8;
    margin-bottom: 10px;
  }

  .files_upload {
    margin-top: 30px;
    .lable_file {
      display: grid;
      grid-template-columns: 60px auto;
      height: 100px;
      width: 100%;
      border: 1px dashed #9b9b9b;
      border-radius: 5px;
      cursor: pointer;
      position: relative;

      i {
        margin: -1px 0 0 -1px;
        border: 1px solid #9b9b9b;
        border-radius: 4px 0 0 4px;
        height: calc(100% + 2px);
        width: 50px;
        font-size: 22px;
        @extend %center;
      }
      p {
        height: 100%;
        line-height: 100%;
        @extend %center;
        word-wrap: break-word;
        overflow: hidden;
      }
    }
  }
  .field {
    display: flex;
    flex-direction: column;
    gap: 0;
    padding-top: 0;

    .tags {
      display: grid;
      grid-gap: 10px;
      height: -moz-fit-content;
      height: fit-content;
      grid-template-columns: auto auto auto;
      margin: 20px 0;
      background: var(--background);
      padding: 10px;
      border-radius: 5px;
      border: 1px solid var(--border);

      .level_radio:checked + label {
        background-color: var(--primary);
        border-color: var(--primary);
      }

      .item {
        background-color: var(--background);
        border: 1px solid var(--border);
        padding: 0 10px;
        border-radius: 5px;
        animation: show_toast_mobile 0.5s linear;
        cursor: pointer;
        min-width: 20%;
        text-align: center;
        display: grid;
        grid-template-columns: 20px auto;
      }
      span {
        width: 100%;
        display: block;
      }
    }
    h3 {
      margin-bottom: 20px;
    }

    textarea {
      resize: vertical;
      min-height: 40px;
      height: 100px;
      padding-top: 10px;
    }
  }
}

@media screen and (max-width: 570px) {
  .add_book_form {
    display: flex;
    flex-direction: column;

    .files_upload {
      width: 85vw;
      margin: auto;

      .lable_file {
        height: 50px;
        margin: 15px 0;
      }
    }
  }
}
