.ToastNotification {
  position: fixed;
  top: 20px;
  right: 40px;
  z-index: 10;
  animation: show_toast 1s ease forwards;

  &.hide {
    animation: hide_toast 1s ease forwards;
  }

  .toast {
    background: var(--background);
    padding: 20px 15px 20px 20px;
    border-radius: 5px;
    border: 1px solid var(--border);
    width: 430px;
    display: grid;
    grid-template-columns: 50px auto 40px;
    align-items: center;
    justify-content: space-between;
    // border-top: 4px solid var(--secondary);
    // border-bottom: 4px solid var(--secondary);
  }
  .icon {
    font-size: 25px;
    height: 50px;
    width: 50px;
    text-align: center;
    line-height: 50px;
    border-radius: 50%;
    i {
      color: #ffffff;
    }
  }
  .details {
    margin-left: 15px;
  }
  .details span {
    font-size: 20px;
    font-weight: 500;
  }
  .details p {
    color: #878787;
  }
  .toast .close-icon {
    color: #ffffff;
    font-size: 23px;
    cursor: pointer;
    height: 30px;
    width: 30px;
    text-align: center;
    line-height: 30px;
    border-radius: 50%;
    background: var(--accent);
    transition: all 0.3s ease;
  }
  .close-icon:hover {
    background: #efefef;
  }
}

@media screen and (max-width: 470px) {
  .ToastNotification {
    right: 15px;
    top: 50px;
    width: calc(100vw - 35px);
    animation: show_toast_mobile 1s ease forwards;

    &.hide {
      animation: hide_toast_mobile 1s ease forwards;
    }
    .toast {
      width: 100%;
    }
  }
}
