#contact_page {
  section {
    display: flex;
    width: 100%;
    .illustration {
      @extend %center;
      img {
        width: 100%;
      }
    }
  }
  main {
    min-width: 100px;
  }
  aside {
    padding: 30px;
    .app_title {
      margin-bottom: 20px;
      font-size: 50px;
      font-family: "Poppins", Arial, sans-serif;
      font-weight: 600;
      line-height: 1.2;
      color: var(--writing);
      strong {
        color: var(--primary);
      }
    }
    .social_icons {
      display: flex;
      gap: 10px;
      margin: 20px 5px;

      a {
        text-decoration: none;
        transition: all 0.2s ease;
      }
      a:hover {
        transform: scale(1.1);
      }

      i {
        height: 40px;
        width: 40px;
        @extend %center;
        border-radius: 50%;
        font-size: 20px;
        color: white;
      }
    }
    .contact_form {
      min-width: 50vw;
      width: 100%;
      form {
        display: flex;
        flex-direction: column;
        gap: 20px;
        input,
        textarea {
          @include input();
        }
        textarea {
          resize: none;
          padding-top: 10px;
          height: 100px;
          font-size: 18px;
        }
        button {
          @extend %button;
        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  #contact_page {
    section {
      flex-direction: column;
    }
    aside {
      padding: 20px;
    }
    aside .contact_form {
      background-color: var(--background);
      min-width: 0;
      width: 100%;
      padding-bottom: 50vh;
    }
  }
}
