#settings_page {
  .theme {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    h3 {
      width: 100%;
      text-align: center;
      margin-bottom: 20px;
      text-transform: capitalize;
    }
    img {
      height: 150px;
      max-width: 300px;
      border: 2px solid var(--border);
      cursor: pointer;
    }
  }
}

@media screen and (max-width: 1080px) {
  #settings_page {
    .theme {
      align-items: center;
      flex-direction: column;
      gap: 10px;
    }
  }
}
@media screen and (max-width: 950px) {
  #settings_page {
    .theme {
      flex-direction: row;
      gap: 0;
    }
  }
}

@media screen and (max-width: 750px) {
  #settings_page {
    .theme {
      h3 {
        margin-bottom: 0;
      }
      flex-direction: column;
      gap: 10px;
    }
  }
}
