.book_card {
  display: grid;
  grid-template-columns: 150px auto;
  gap: 10px;

  .info {
    margin: 10px 0;
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 12px;
  }
  a {
    text-decoration: none;
    color: var(--writing);
    font-family: "time new roman";
    font-weight: 400;
    text-transform: capitalize;
  }
  .image {
    height: 200px;
    width: 150px;
    // border-radius: 50%;
    border-radius: 8px 3px 3px 8px;
    background-size: cover;
    background-color: var(--background);
    border-left: 8px solid var(--primary);
    box-shadow: 2px 0 5px var(--shadow);
    transition: all 0.15s ease-in-out;

    &:hover {
      box-shadow: -2px 0 5px var(--shadow);
    }
    i {
      position: absolute;
      top: -2px;
      left: 125px;
      font-size: 22px;
      color: var(--primary);
    }
  }
  .text-content {
    padding-left: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .description {
      color: gray;
      @include clamp;
    }
    .read_more {
      padding: 10px 0;
    }
    .read_more a {
      color: var(--primary);
      font-family: $font;
    }
    h3 {
      font-size: 1.6em;
    }
    a {
      @include clamp;
    }
  }
}

@media screen and (max-width: 850px) {
  .book_card .text-content a {
    @include clamp(5);
  }
}
