.layout {
  display: flex;
  width: 100vw;

  nav {
    // width: 300px;
  }
  main {
    background-color: var(--background);
    padding: 20px;
    padding-top: 20px;
    width: 100%;
    min-width: 450px;
  }
  aside {
    background-color: var(--background);
  }
}
@media screen and (max-width: 1050px) {
  #root {
    overflow-x: hidden;
  }
}
@media screen and (max-width: 950px) {
  .layout {
    main {
      padding-top: 40px;
    }
  }
}
@media screen and (max-width: 450px) {
  .layout {
    main {
      min-width: 100vw;
      max-width: 100vw;
      padding-top: 40px;
    }
  }
}
